import '@main';

export class LocaleDropdown {
  options = {
    ajaxUrl: '',
    currentId: '',
  };

  constructor(el, options) {
    // the modal element
    this.el = el;
    this.options = Object.assign({}, this.options, options);
  }
}

u.directive(
  'locale-dropdown',
  {
    mounted(el, { value }) {
      const options = JSON.parse(value);

      u.module(el, 'locale.dropdown', () => new LocaleDropdown(el, options));
    }
  }
);

